import { BaseEntity } from "../../master/masters/base-entity";
import { Department } from "../../master/department/masters/department";
import { LookupEntity } from "../../master/lookup-module/masters/lookup-entity";
import { Branch } from "../../master/service-center/masters/branch";
import { Employee } from "../../master/unit-master/masters/Employee";
import { Unit } from "../../master/unit-master/masters/unit";
import { Patient } from "../../patient/master/patient";


export class Appointment extends BaseEntity{
	constructor(){
		super();
	}
	appointmentId: number;
	branch: Branch = new Branch();
	department :Department = new Department();
	unit: Unit = new Unit();
	doctor: Employee = new Employee();
	patient: Patient = new Patient();
	fromDate : Date = new Date();
	toDate : Date = new Date();
	appointment : String = "appointment";
	appointmentDateTime: Date = new Date() ;
	appointmentStatus : LookupEntity;
	appointmentStatusType : LookupEntity;
	visitDate: Date = new Date();
}
